"use client"

import React, { useEffect, useRef, useState } from "react"
import { AlertError } from "@/components/alertError"
import { Button } from "@/components/shadcn/ui/button"
import {
  Dialog,
  Card,
  CardContent,
  CardHeader,
  CardTitle,
} from "@/components/shadcn/ui"
import { Input } from "@/components/shadcn/ui/input"
import { FilterIcon, Star, StarOff, Globe, Lock } from "lucide-react"
import { Badge } from "@/components/shadcn/ui/badge"



import { useSession } from "next-auth/react"
import { useRouter } from "next/navigation"
import { InsertLibraryForm } from "./insertLibraryForm"

import { LANGUAGES, LibraryProps } from "./layout"

function LibraryCard({ item }: { item: LibraryProps }) {
  const [isStarred, setIsStarred] = useState(item.isStarred)
  const router = useRouter()

  const toggleStarred = async (e: React.MouseEvent) => {
    e.stopPropagation() // chặn click làm chuyển trang
    try {
      const res = await fetch(`/api/dictionary/library/${item._id}`, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        
      })
      if (res.ok) {
        setIsStarred((prev) => !prev)
      } else {
        console.error("Failed to toggle starred")
      }
    } catch (err) {
      console.error("Error toggling starred:", err)
    }
  }

  return (
    <div className="rounded-xl bg-gray-100 dark:bg-gray-800 p-4 shadow">
      <div className="flex justify-between items-start mb-3">
        <div className="flex items-center space-x-2">
          <Button
            variant="link"
            className="text-base text-blue-700 font-semibold p-0"  
            onClick={() =>
              router.push(`/education/languages/vocabulary/library/${item._id}/${item.title}`)
            }
          >
            {item.title}
          </Button>
          <Badge variant="outline" className="ml-auto text-xs p-1">
            {item.isPublic ? "Công khai" : "Riêng tư"}
          </Badge>
        </div>
        <Button variant="ghost" size="icon" onClick={toggleStarred}>
          {isStarred ? (
            <Star className="w-4 h-4 text-yellow-400" />
          ) : (
            <StarOff className="w-4 h-4 text-gray-400" />
          )}
        </Button>
      </div>

      <div className="flex items-center space-x-2">
        # {item.language}
        <Badge variant="outline" className="ml-auto text-xs">
          <span className="font-medium">Cập nhật:</span>{" "}
          {new Date(item.updatedAt).toLocaleDateString("vi-VN")}
        </Badge>
      </div>
    </div>
  )
}

export function MyCard() {
  const [visibleItems, setVisibleItems] = useState<LibraryProps[]>([])
  const [dataList, setDataList] = useState<LibraryProps[]>([]);
  const itemsPerLoad = 12
  const loadMoreRef = useRef<HTMLDivElement | null>(null)
  const loadedCountRef = useRef<number>(0)

  const [error, setError] = useState<string | null>(null)
  const { data: session, status } = useSession()

  
  useEffect(() => {
    const initialItems = dataList.slice(0, itemsPerLoad)
    setVisibleItems(initialItems)
    loadedCountRef.current = itemsPerLoad
  }, [])

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        const isVisible = entries[0].isIntersecting
        if (isVisible) {
          const next = dataList.slice(
            loadedCountRef.current,
            loadedCountRef.current + itemsPerLoad
          )
          if (next.length > 0) {
            setVisibleItems((prev) => [...prev, ...next])
            loadedCountRef.current += itemsPerLoad
          }
        }
      },
      {
        rootMargin: "0px",
        threshold: 1.0,
      }
    )

    if (loadMoreRef.current) {
      observer.observe(loadMoreRef.current)
    }

    return () => {
      if (loadMoreRef.current) {
        observer.unobserve(loadMoreRef.current)
      }
    }
  }, [])

  useEffect(() => {
    if (status === "loading") return
    const user = session?.user

    if (!user) {
      setError("Bạn cần đăng nhập để xem thư viện")
      return
    }
    const fetchData = async () => {
      try {
        const res = await fetch(`/api/dictionary/library/userId`, {
          method: "GET",
          headers: {
            'Content-Type': 'application/json',
            'userId': user.id ?? '', 
          },
        });

        if (!res.ok) {
          throw new Error("Failed to fetch libraries");
        }

        const data = await res.json();
        setDataList(data);
        const initialItems = data.slice(0, itemsPerLoad)
      setVisibleItems(initialItems)
      loadedCountRef.current = initialItems.length
      } catch (error) {
        console.error("Error fetching libraries:", error);
      }
    }
    fetchData()
  }, [session?.user])

  return (
    <>
      <Card>
        <CardHeader>
          <CardTitle>Thư viện của tôi</CardTitle>
          <div className="flex items-center justify-between mt-2">
            <div className="flex items-center space-x-2">
              <Input placeholder="Tìm kiếm..." className="w-[200px]" />
              <Button variant="outline" size="icon">
                <FilterIcon className="h-4 w-4" />
              </Button>
            </div>
            <Dialog>
              <InsertLibraryForm
                setDataList={setDataList}
                setVisibleItems={setVisibleItems}
              />
            </Dialog>
          </div>
          {error && (
            <AlertError>
              {error}
            </AlertError>
          )}
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 gap-4">
            {visibleItems.map((item) => (
              <LibraryCard key={item._id} item={item} />
            ))}
          </div>
          <div
            ref={loadMoreRef}
            className="h-10 mt-4 flex justify-center items-center"
          >
            {visibleItems.length < dataList.length ? (
              <span className="text-sm text-muted-foreground">
                Đang tải thêm...
              </span>
            ) : (
              <span className="text-sm text-muted-foreground">
                Đã tải hết nội dung
              </span>
            )}
          </div>
        </CardContent>
      </Card>
    </>
  )
}
